@charset "UTF-8";
*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  font-size: 10px;
  text-size-adjust: 100%; }

body {
  background-color: #f3f3f3;
  color: #333;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Hiragino Sans GB", "Microsoft Yahei", "微软雅黑", Arial, Helvetica, STHeiti, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 1.8rem;
  line-height: 1.5;
  margin: 0; }

p,
blockquote,
table,
hr,
ul,
ol {
  margin: 15px 0 0; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
  margin: 30px 0 0; }

h1 {
  font-size: 3rem; }

h2 {
  font-size: 2.4rem; }

h3 {
  font-size: 2rem; }

h4 {
  font-size: 1.8rem; }

h5 {
  font-size: 1.8rem; }

h6 {
  font-size: 1.8rem; }

a {
  color: #0080ff;
  text-decoration: none; }
  a:hover, a:active {
    text-decoration: underline; }

b,
strong {
  font-weight: 600; }

img {
  max-width: 100%; }

code {
  background-color: rgba(0, 0, 0, 0.05);
  font-family: Consolas, "Liberation Mono", Menlo, Courier, monospace;
  font-size: 85%;
  padding: 2px 3px; }

pre {
  background-color: rgba(0, 0, 0, 0.05);
  font-size: 1.5rem;
  line-height: 1.2;
  margin-left: -15px;
  margin-right: -15px;
  max-height: 300px;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  padding: 15px; }
  pre code {
    background-color: transparent;
    padding: 0; }

blockquote {
  border-left: 5px solid #ddd;
  color: #777;
  padding-left: 15px; }

table {
  border: 0;
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%; }

th,
td {
  border: 0;
  border-bottom: 1px solid #ddd;
  padding: 5px;
  text-align: left; }

th {
  background-color: rgba(0, 0, 0, 0.05);
  font-weight: 600; }

form {
  margin: 0; }
  form label {
    cursor: pointer;
    display: block;
    margin-top: 15px; }

a,
button,
input,
select,
textarea,
label {
  outline: 0;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0.1);
  touch-action: manipulation; }

button,
html [type=button],
[type=reset],
[type=submit],
::-webkit-file-upload-button {
  -webkit-appearance: button; }

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px; }

select,
textarea,
[type=search]::-webkit-search-cancel-button,
[type=search]::-webkit-search-decoration,
input:not([type=checkbox]):not([type=radio]) {
  -webkit-appearance: none; }

::-webkit-file-upload-button {
  font: inherit; }

[hidden] {
  display: none; }

button,
select,
textarea,
input:not([type=checkbox]):not([type=radio]) {
  display: block;
  line-height: 1.5;
  margin: 15px 0 0;
  width: 100%; }

button,
select,
textarea,
[type=button],
[type=date],
[type=datetime-local],
[type=email],
[type=month],
[type=number],
[type=password],
[type=range],
[type=search],
[type=submit],
[type=tel],
[type=text],
[type=time],
[type=url],
[type=week] {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 3px;
  color: #333;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Hiragino Sans GB", "Microsoft Yahei", "微软雅黑", Arial, Helvetica, STHeiti, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 1.8rem;
  padding: 5px 10px; }
  button:focus,
  select:focus,
  textarea:focus,
  [type=button]:focus,
  [type=date]:focus,
  [type=datetime-local]:focus,
  [type=email]:focus,
  [type=month]:focus,
  [type=number]:focus,
  [type=password]:focus,
  [type=range]:focus,
  [type=search]:focus,
  [type=submit]:focus,
  [type=tel]:focus,
  [type=text]:focus,
  [type=time]:focus,
  [type=url]:focus,
  [type=week]:focus {
    border-color: #3399ff; }

[type=checkbox],
[type=radio] {
  margin: 13px 5px; }

[type=file] {
  padding-bottom: 6px;
  padding-top: 6px; }

button,
[type=button],
[type=submit],
[type=reset] {
  background-color: #f3f3f3;
  cursor: pointer; }

select {
  background-image: url("data:image/png;base64,");
  background-position: right 5px top 11px;
  background-repeat: no-repeat;
  background-size: 16px 16px;
  padding-right: 30px; }

[disabled] {
  background-color: #f3f3f3;
  color: #777;
  cursor: not-allowed; }
  [disabled]::-webkit-input-placeholder {
    color: #777; }

ul,
ol {
  padding-left: 30px; }

hr {
  border: 0;
  border-top: 1px solid #ddd; }

.container {
  background-color: #fff;
  margin: 0 auto;
  max-width: 768px;
  min-width: 320px;
  outline: 15px solid #fff;
  overflow: hidden;
  padding: 0 15px 15px;
  width: 100%; }

.container-side {
  display: none;
  padding: 0 30px 15px;
  width: 256px; }

@media (min-width: 1041px) {
  body {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center; }
  .container {
    flex-grow: 1;
    margin: 0;
    outline: 0;
    padding: 0 30px 15px; }
  .container-side {
    display: block; } }

.row {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  margin-left: -15px;
  margin-right: -15px; }

.col,
.col-1-2,
.col-1-3,
.col-1-4,
.col-custom {
  padding-left: 7.5px;
  padding-right: 7.5px; }
  .col:first-child,
  .col-1-2:first-child,
  .col-1-3:first-child,
  .col-1-4:first-child,
  .col-custom:first-child {
    padding-left: 15px; }
  .col:last-child,
  .col-1-2:last-child,
  .col-1-3:last-child,
  .col-1-4:last-child,
  .col-custom:last-child {
    padding-right: 15px; }

.col {
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%; }

.col-1-2,
.col-1-3,
.col-1-4,
.col-custom {
  flex-shrink: 0; }

.col-1-2 {
  flex-basis: 50%;
  max-width: 50%; }

.col-1-3 {
  flex-basis: 33.33%;
  max-width: 33.33%; }

.col-1-4 {
  flex-basis: 25%;
  max-width: 25%; }

.btn-primary {
  background-color: #40bf40;
  border: 0;
  color: #fff; }
  .btn-primary[disabled] {
    background-color: #8cd98c;
    border: 0; }

.btn-danger {
  color: #cc0000; }
  .btn-danger[disabled] {
    color: #ff3333; }

.mt-30 {
  margin-top: 30px; }

.mt-15 {
  margin-top: 15px; }

.mt-0 {
  margin-top: 0; }

.text-left {
  text-align: left; }

.text-center {
  text-align: center; }

.text-right {
  text-align: right; }

.scroll-view {
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  width: 100%; }

.label {
  margin-top: 15px;
  padding-top: 6px; }
